#logo-tiny {
    height: 25px;
    margin: 0;
}

#how-page p {
    margin-left: 20px;
    padding: 5px 0;
    line-height: 1.4;
    text-align: justify;
}

#how-page h3 {
    text-align: left;
    margin: 15px 0 5px 0;
}

#how-page .button {
    border: 1px solid gray;
    border-radius: 2px;
    padding: 4px;
}

#timer-anim-ex {
    display: inline-block;
    height: 12px;
    width: 12px;
    margin: 0;
    border: 2px solid rgba(40,40,40,0.5);
    border-radius: 4px;
    background-color: transparent;
}

.no-break {
    white-space: nowrap;
}

.fine-print p {
    margin: 0 !important;
    padding: 0 !important;
    font-size: 14px;
}
