form > * {
    display: block;
    text-align: left;
}

form label {
    margin: 0 0 2px 20px;
}

form > *:not(label) {
    margin: 0 0 10px 20px;
    padding: 4px;
}

form .extend {
    width: 80%;
    max-width: 400px;
}

form textarea {
    min-height: 80px;
    font-family: inherit;
}

form input[type="submit"]:disabled {
    cursor: progress;
}

form option {
    font-size: 11pt;
}

form .required:not(span)::after {
    content: '*';
    color: red;
}

form span.required::before {
    content: '*';
    color: red;
}

form .inline-group {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 0;
}

form .inline-group > label {
    margin: 0 0 0 10px;
}

form input[type="checkbox"] {
    height: 1.2rem;
    width: 1.2rem;
    margin: 0;
}
